import { useState, useEffect } from "react";
import Map from "./components/Map";
import GuessArea from "./components/GuessArea";
import Header from "./components/Header";

const mapUrl =
    "https://raw.githubusercontent.com/biela000/coding/main/Web/Other/flag_quiz/src/country-db.json";

export default function App() {
    function shuffle(array) {
        const shuffledArray = [...array];
        for (let i = shuffledArray.length - 1; i !== 0; i--) {
            const randomIndex = Math.floor(Math.random() * i);
            [shuffledArray[i], shuffledArray[randomIndex]] = [
                shuffledArray[randomIndex],
                shuffledArray[i],
            ];
        }
        return shuffledArray;
    }
    async function fetchGeography() {
        const res = await fetch(mapUrl);
        const data = await res.json();
        return data;
    }
    const [guessedCountries, setGuessedCountries] = useState([]);
    useEffect(() => {
        fetchGeography().then((res) =>
            setGuessedCountries(
                res.objects.ne_110m_admin_0_countries.geometries
            )
        );
    }, []);
    const [guessInputInfo, setGuessInputInfo] = useState({
        countryName: "",
        countryCapital: "",
        clickedCountry: "",
        clickedCountryObject: null,
    });

    function handleClickedCountryChange(countryName, event) {
        setGuessInputInfo((prevGuessInputInfo) => {
            return {
                ...prevGuessInputInfo,
                clickedCountry: countryName,
                clickedCountryObject: event.target,
            };
        });
        if (
            countryName ===
            guessedCountries[currentGuessedCountryIndex].properties.NAME
        ) {
            event.target.classList.add("correct-country");
        }
    }
    function handleGuessInputInfoChange(event) {
        const { name, value } = event.target;
        setGuessInputInfo((prevGuessInputInfo) => {
            return {
                ...prevGuessInputInfo,
                [name]: value,
            };
        });
    }
    const [isGameStarted, setIsGameStarted] = useState(false);
    const [score, setScore] = useState(0);
    const [currentGuessedCountryIndex, setCurrentGuessedCountryIndex] =
        useState(0);
    function startGame(event) {
        setIsGameStarted(true);
        setGuessedCountries((prevGuessedCountries) =>
            shuffle(prevGuessedCountries)
        );
        event.target.disabled = true;
    }
    function skipCountry() {
        if (guessInputInfo.clickedCountryObject !== null) {
            if (
                guessInputInfo.clickedCountryObject.classList.contains(
                    "correct-country"
                )
            ) {
                guessInputInfo.clickedCountryObject.classList.remove(
                    "correct-country"
                );
            }
        }
        setGuessInputInfo({
            countryName: "",
            countryCapital: "",
            clickedCountry: "",
            clickedCountryObject: null,
        });
        setCurrentGuessedCountryIndex(
            (prevCurrentGuessedCountryIndex) =>
                prevCurrentGuessedCountryIndex + 1
        );
    }
    if (guessedCountries.length > 0) {
        const countryProps =
            guessedCountries[currentGuessedCountryIndex].properties;
        if (
            guessInputInfo.countryName === countryProps.NAME &&
            guessInputInfo.countryCapital === countryProps.CAPITAL &&
            guessInputInfo.clickedCountry === countryProps.NAME
        ) {
            setCurrentGuessedCountryIndex(
                (prevCurrentGuessedCountryIndex) =>
                    prevCurrentGuessedCountryIndex + 1
            );
            setGuessInputInfo({
                countryName: "",
                countryCapital: "",
                clickedCountry: "",
                clickedCountryObject: null,
            });
            setScore((prevScore) => prevScore + 1);
        }
    }
    function endGame() {}
    return (
        <div className="app">
            <Header score={score} countriesDone={currentGuessedCountryIndex} />
            <Map
                geoUrl={mapUrl}
                handleClick={handleClickedCountryChange}
                isGameStarted={isGameStarted}
                clickedCountryObject={guessInputInfo.clickedCountryObject}
            />
            <GuessArea
                guessAreaInputInfo={guessInputInfo}
                handleChange={handleGuessInputInfoChange}
                currentCountryCode={
                    guessedCountries.length > 0
                        ? guessedCountries[
                              currentGuessedCountryIndex
                          ].properties.ISO_A2.toLowerCase()
                        : "af"
                }
                startGame={startGame}
                isGameStarted={isGameStarted}
                correctCountryName={
                    guessedCountries.length > 0
                        ? guessedCountries[currentGuessedCountryIndex]
                              .properties.NAME
                        : "a"
                }
                correctCountryCapital={
                    guessedCountries.length > 0
                        ? guessedCountries[currentGuessedCountryIndex]
                              .properties.CAPITAL
                        : "a"
                }
                handleSkipCountry={skipCountry}
            />
        </div>
    );
}
