import "./css/guess-area.css";

export default function GuessArea(props) {
    return (
        <div className="guess-area">
            <img
                src={`./images/${props.currentCountryCode}.svg`}
                alt="Country flag"
                className="country-flag-img"
            />
            <div className="guess-area--input-container">
                <input
                    type="text"
                    name="countryName"
                    className="guess-area--input"
                    onChange={props.handleChange}
                    value={props.guessAreaInputInfo.countryName}
                    placeholder="nazwa pastwa"
                    disabled={
                        !props.isGameStarted ||
                        props.guessAreaInputInfo.countryName ===
                            props.correctCountryName
                    }
                />
                <span
                    className="input-indicator"
                    style={{
                        backgroundColor:
                            props.guessAreaInputInfo.countryName ===
                            props.correctCountryName
                                ? "var(--color3)"
                                : "",
                        transform:
                            props.guessAreaInputInfo.countryName ===
                            props.correctCountryName
                                ? "scaleX(1)"
                                : "",
                    }}
                ></span>
            </div>
            <div className="guess-area--input-container">
                <input
                    type="text"
                    name="countryCapital"
                    className="guess-area--input"
                    onChange={props.handleChange}
                    value={props.guessAreaInputInfo.countryCapital}
                    placeholder="stolica"
                    disabled={
                        !props.isGameStarted ||
                        props.guessAreaInputInfo.countryCapital ===
                            props.correctCountryCapital
                    }
                />
                <span
                    className="input-indicator"
                    style={{
                        backgroundColor:
                            props.guessAreaInputInfo.countryCapital ===
                            props.correctCountryCapital
                                ? "var(--color3)"
                                : "",
                        transform:
                            props.guessAreaInputInfo.countryCapital ===
                            props.correctCountryCapital
                                ? "scaleX(1)"
                                : "",
                    }}
                ></span>
            </div>
            <button
                className="guess-area--button start-button"
                onClick={props.startGame}
            >
                start
            </button>
            <button
                className="guess-area--button"
                disabled={!props.isGameStarted}
                onClick={props.handleSkipCountry}
            >
                pomi
            </button>
        </div>
    );
}
