import {
    ComposableMap,
    Geographies,
    Geography,
    ZoomableGroup,
} from "react-simple-maps";
import "./css/map.css";

export default function Map(props) {
    return (
        <div
            className="map"
            style={
                props.clickedCountryObject !== null
                    ? {
                          pointerEvents:
                              props.clickedCountryObject.classList.contains(
                                  "correct-country"
                              )
                                  ? "none"
                                  : "",
                      }
                    : {}
            }
        >
            <ComposableMap data-tip="">
                <ZoomableGroup zoom={1}>
                    {" "}
                    <Geographies geography={props.geoUrl}>
                        {({ geographies }) =>
                            geographies.map((geo) => {
                                return (
                                    <Geography
                                        key={geo.rsmKey}
                                        geography={geo}
                                        onClick={
                                            props.isGameStarted
                                                ? (event) =>
                                                      props.handleClick(
                                                          geo.properties.NAME,
                                                          event
                                                      )
                                                : () => {}
                                        }
                                        style={{ fill: "blue" }}
                                    />
                                );
                            })
                        }
                    </Geographies>
                </ZoomableGroup>
            </ComposableMap>
        </div>
    );
}
